.rebate-box {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top: 1px solid #203b60;

  .loaded-copied-logo {
    display: none;
  }

  .rebate-box-info {
    width: 1140px;

    .rebate-box-info-titile {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 96px;
      font-size: 32px;
      color: #fff;

      .rebate-des {
        font-size: 14px;
        color: #93a1b8;
        width: 347px;
      }

    }

    .rebate-box-header {
      width: 100%;
      height: 200px;
      display: flex;
      align-items: center;
      padding: 0 36px;
      border: 1px solid #203B60;
      position: relative;
      // background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .rebate-avatar {
        position: relative;

        .explained-logo {
          position: absolute;
          right: -10px;
          top: 0;
        }
      }

      .rebate-account-reward-info {
        font-weight: 500;
        font-size: 20px;
        color: #93A1B8;
        margin-left: 36px;

        .rebate-reward {
          margin-bottom: 36px;
          display: flex;
          align-items: center;
          cursor: pointer;

          .reward-btn {
            display: flex;
            min-width: 100px;
            height: 32px;
            align-items: center;
            justify-content: flex-end;
            padding: 0 8px;
            background: #5577FD;
            border-radius: 80.6452px;
            margin: 0 8px;
            color: #E0ECFF;

            .token-icon-reward {
              width: 24px;
              height: 24px;
              display: flex;
              align-items: center;
              justify-content: center;
              background-color: #FFF;
              border-radius: 50%;
              margin-left: 12px;

              img {
                width: 18px;
                height: 18px;
              }
            }

          }

          .no-reward {
            background: rgb(85, 119, 253);
            opacity: 0.3;
            cursor: no-drop;
          }
        }

        .rebate-code {
          display: flex;
          align-items: center;

          .no-code-hash {
            background: #3756CD;
            border-radius: 4px;
            width: 144px;
            height: 32px;
            font-weight: 600;
            font-size: 16px;
            color: #E0ECFF;
            margin-left: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
          }

          .no-wallet {
            background: #203B60;
            border: 1px solid #586B88;
            color: #93A1B8;
          }

          .no-recruiter-code-hash {
            width: 96px;
            height: 32px;
            background: #203B60;
            border-radius: 4px;
            color: #E0ECFF;
            margin-left: 8px;
            display: flex;
            align-items: center;
            font-weight: 600;
            font-size: 16px;
            justify-content: center;

            img {
              margin-right: 4px;
            }
          }

          .code-hash {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 160px;
            height: 32px;
            cursor: pointer;
            border-radius: 6px;
            font-size: 20px;
            color: #E0ECFF;
            margin-left: 24px;
            background-image: url('./img/code-border.svg');
            background-size: 100% 100%;


            img {
              margin-left: 12px;
            }


          }

          .code-hash-copy {
            display: flex;

            .copied-box {
              display: flex;
              align-items: center;
              justify-content: center;
              font-size: 14px;
              color: #FFFFFF;
              margin-left: 12px;

              img {
                margin-right: 4px;
              }
            }

            .copied-m {
              display: none;
            }
          }
        }
      }

      .broker-recruiter {
        position: absolute;
        right: 36px;
        top: 25px;
        width: 139px;
        height: 32px;
        cursor: pointer;
        background: linear-gradient(270deg, #203B60 68.12%, rgba(32, 59, 96, 0) 117.79%);
        border-radius: 1000px;

        .broker-type,
        .recruiter-type {
          width: 97px;
          height: 32px;
          display: flex;
          align-items: center;
          justify-content: center;
          justify-content: center;
          background: radial-gradient(83.25% 291.37% at 32.14% 100%, #CD7A37 0%, #FFB77D 100%);
          box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.15);
          border-radius: 1000px;
          font-weight: 600;
          font-size: 12px;
          color: #FFFFFF;
          cursor: pointer;
          position: absolute;
          transition: left 0.2s;
        }

        .broker-type {
          left: 0;
        }

        .recruiter-type {
          background: radial-gradient(83.25% 291.37% at 32.14% 100%, #59AE99 0%, #92FFE4 100%);
          text-align: center;
          left: 42px;
        }
      }
    }

    .rebate-box-use-info {
      width: 100%;
      height: 320px;
      border: 1px solid #203B60;
      // background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
      margin-top: 25px;
      padding: 52px 46px 70px 46px;

      .rebate-box-user-info-header {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .rebate-box-user-info-title {
          font-weight: 500;
          font-size: 24px;
          color: #E0ECFF;

          .rebate-box-user-info-title-time-m {
            display: none;
          }

          span {
            font-size: 14px;
            color: #93A1B8;
          }
        }

        .wallet-account-box,
        .acc-wrapper {
          width: 192px;
          height: 48px;
          border: 1px solid #203B60;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: transparent;
          font-weight: 700;
          font-size: 16px;
          color: #E0ECFF;

          img {
            margin-right: 12px;
          }
        }

        .acc-wrapper:hover {
          background: transparent;
        }
      }

      .rebate-box-use-info-box {
        width: 1052px;
        height: 96px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 74px;

        .total-rebate-bg {
          width: 336px;
          height: 96px;
          border-radius: 4px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: linear-gradient(255.96deg, #CD7A37 0%, #E0ECFF 49.48%, #5577FD 100%);

          .total-rebate-box {
            width: 334px;
            height: 94px;
            border-radius: 4px;
            background-color: #0A172B;
            background-image: url('./img/total-users.png');
            background-size: 100% 100%;
            display: flex;
            align-items: center;
            padding-left: 24px;

            img {
              margin-right: 14px;
            }

            .total-rebate-box-info {
              font-size: 24px;
              color: #E0ECFF;

              .total-rebate-box-info-title {
                font-size: 20px;
                color: #93A1B8;
              }
            }
          }

          .total-rewards-box {
            background-image: url('./img/total-rewards.png');
          }

          .total-fee-box {
            background-image: url('./img/total-fee.png');
          }
        }
      }
    }

    .rebate-box-explained {
      width: 100%;
      height: 540px;
      border: 1px solid #203B60;
      margin-top: 25px;
      padding: 44px;
      margin-bottom: 78px;
      // background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

      .rebate-box-explained-title {
        color: #E0ECFF;
        font-size: 20px;
        font-weight: 500;
        display: flex;
        align-items: center;

        img {
          margin-left: 8px;
        }
      }

      .rebate-box-explained-list {
        width: 100%;
        height: 100%;

        .rebate-box-explained-list-box {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 30px;

          .explained-box {
            width: 512px;
            height: 104px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: linear-gradient(255.96deg, #CD7A37 0%, #E0ECFF 49.48%, #5577FD 100%);
            border-radius: 4px;

            .explained-box-info {
              width: 510px;
              height: 102px;
              display: flex;
              align-items: center;
              border-radius: 4px;
              padding: 0 24px;
              font-weight: 500;

              .explained-text {
                margin-left: 24px;

                .explained-rate {
                  font-size: 20px;
                  color: #E0ECFF;
                }
              }
            }

            .explained-one {
              background-color: #233044;
            }

            .explained-two {
              background-color: #1c273d;
            }

            .explained-three {
              background-color: #142342;
            }

            .explained-four {
              background-color: #1a222f;
            }

            .explained-five {
              background-color: #1d212c;
            }

            .explained-six {
              background-color: #262132;
            }
          }
        }

        .rebate-explained-margin-box {
          margin-top: 51px;
        }
      }
    }

  }
}

@media only screen and (max-width: 1140px) {
  .rebate-box {
    .rebate-box-info {
      width: 334px;

      .rebate-box-info-titile {
        height: 69px;
        margin-bottom: 25px;

        .rebate-des {
          display: none;
        }
      }

      .rebate-box-header {
        flex-wrap: wrap;
        height: 100%;
        padding: 20px;

        .rebate-account-reward-info {
          margin-left: 0;
          margin-top: 20px;

          .rebate-code {
            flex-wrap: wrap;

            .code-hash-copy {
              .copied-pc {
                display: none;
              }
            }

            .copy-code-pc {
              display: none;
            }



            .code-hash-copy {
              width: 290px;
              margin-top: 10px;

              .copied-m {
                display: flex;
              }

              .code-hash {
                width: 100%;
                height: 50px;
                padding: 0 28px;
                justify-content: space-between;
                margin-left: 0;
                background-image: url('./img/code-border-m.svg');
              }
            }
          }

          .rebate-reward {
            flex-wrap: wrap;
          }
        }

        .broker-recruiter {
          width: 104px;
          right: 21px;

          .broker-type,
          .recruiter-type {
            width: 73px;
          }

          .recruiter-type {
            left: 30px;
          }
        }
      }

      .rebate-box-use-info {
        margin-top: 32px;
        height: 100%;
        padding: 24px;

        .rebate-box-user-info-header {
          flex-wrap: wrap;

          .rebate-box-user-info-title {
            display: flex;
            flex-direction: column;
            margin-bottom: 12px;

            .rebate-box-user-info-title-time-pc {
              display: none;
            }

            .rebate-box-user-info-title-time-m {
              display: block;
            }

            span {
              margin: 0;
            }
          }

        }

        .rebate-box-use-info-box {
          width: 100%;
          flex-wrap: wrap;
          margin-top: 14px;
          height: 100%;


          .total-rebate-bg {
            width: 284px;
            height: 56px;
            margin-top: 20px;

            .total-rebate-box {
              width: 282px;
              height: 54px;
              background-image: url('./img/total-users-m.png');

              .total-rebate-box-info {
                display: flex;
                align-items: center;
                font-size: 16px;

                .total-rebate-box-info-num {
                  margin-right: 8px;
                  font-size: 20px;
                }

                .total-rebate-box-info-title {
                  font-size: 16px;
                }
              }

              img {
                width: 32px;
                height: 32px;
              }
            }

            .total-rewards-box {
              background-image: url('./img/total-rewards-m.png');
            }

            .total-fee-box {
              background-image: url('./img/total-fee-m.png');
            }
          }
        }
      }

      .rebate-box-explained {
        height: 100%;
        margin-top: 64px;
        border: 0;
        padding: 0;

        .rebate-box-explained-list {
          .rebate-box-explained-list-box {
            margin-top: 0;
            flex-wrap: wrap;

            .explained-box {
              width: 334px;
              margin-top: 30px;

              .explained-box-info {
                width: 332px;

                .explained-text {
                  .explained-rate {
                    font-size: 16px;
                  }

                  .explained-range-usd {
                    font-size: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}