.register-modal-box {
  width: 360px;
  height: 308px;
  background: #0A172B;
  // border: 1px solid #203B60;
  border-radius: 4px;

  .register-modal-header {
    width: 100%;
    height: 62px;
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #E0ECFF;
    font-size: 22px;
    font-weight: 600;
    // border-bottom: 1px solid rgba(147, 161, 184, 0.5);

    img {
      cursor: pointer;
    }
  }

  .register-modal-info {
    padding: 0 20px;
    margin-top: 20px;
    font-weight: 500;
    font-size: 20px;
    color: #E0ECFF;

    input {
      width: 100%;
      height: 40px;
      // border: 1px solid rgba(147, 161, 184, 0.5);
      border-radius: 4px;
      border: 0;
      background-color: rgba(85, 119, 253, 0.1);
      padding-left: 16px;
      font-size: 14px;
      font-weight: 500;
      outline: none;
      color: #E0ECFF;
    }
    input::placeholder{
      color: #93A1B8;
    }

    .register-title {
      margin-top: 8px;
      margin-bottom: 8px;
      font-size: 14px;
      color: #93A1B8;
    }

    .confirm-btn {
      margin-top: 44px;
      position: relative;
    }

    .noLegitimateText {
      position: absolute;
      display: flex;
      align-items: center;
      left: 0;
      font-weight: 500;
      font-size: 16px;
      color: #CE5E5E;
      top: -26px;
      img{
        margin-right: 8px;
      }
    }
  }
}